import React from "react";
import {
  addLiveStreamSupport,
  removeLoadMetadataEventListner
} from "../../utils/liveStreamUtil";
// const videoUrl = require("./sample_video.mp4");
import "./playerView.scss";

const remoteActions = {
  UP: 38,
  DOWN: 40,
  LEFT: 37,
  RIGHT: 39,
  SELECT: 13,
  BACK: 8,
  REWIND: 227,
  PLAY_PAUSE: 179,
  FAST_FORWARD: 228
};

class PlayerView extends React.Component {
  constructor(props) {
    super(props);
    this.videoRef = React.createRef();
    this.seekBarRef = React.createRef();
    this.playPauseVideo = this.playPauseVideo.bind(this);
  }
  componentDidMount() {
    debugger;
    const that = this;
    window.addEventListener("keyup", e => {
      console.log(e);
      if (e.keyCode === remoteActions.SELECT) {
        that.playPauseVideo();
      } else if (e.keyCode === remoteActions.REWIND) {
        that.decreaseSeekBarByTenSeconds();
      } else if (e.keyCode === remoteActions.FAST_FORWARD) {
        that.increaseSeekBarByTenSeconds();
      } else if (e.keyCode === remoteActions.LEFT) {
        that.decreaseSeekBarByTenSeconds();
      } else if (e.keyCode === remoteActions.RIGHT) {
        that.increaseSeekBarByTenSeconds();
      } else if (e.keyCode === remoteActions.PLAY_PAUSE) {
        that.playPauseVideo();
      }
    });
    if (this.props.live) {
      const video = document.getElementById("liveVideo");
      addLiveStreamSupport(video);
    }
  }

  componentWillUnmount() {
    if (this.props.live) {
      const video = document.getElementById("liveVideo");
      removeLoadMetadataEventListner(video);
    }
  }

  playPauseVideo = () => {
    // const videoRef = document.getElementById("custom_video");
    console.log(this, this.videoRef.current);
    if (this.videoRef.current.paused) {
      this.videoRef.current.play();
      this.videoRef.current.innerHTML = "Pause";
    } else {
      this.videoRef.current.pause();
      this.videoRef.current.innerHTML = "Play";
    }
    // if (videoRef.paused) {
    //   videoRef.play();
    //   videoRef.innerHTML = "Pause";
    // } else {
    //   videoRef.pause();
    //   videoRef.innerHTML = "Play";
    // }
  };

  increaseSeekBarByTenSeconds = () => {
    const videoRef = this.videoRef.current;
    const seekBarRef = this.seekBarRef.current;
    const time = videoRef.duration * (seekBarRef.value / 100);
    videoRef.currentTime = time + 10;
  };

  decreaseSeekBarByTenSeconds = () => {
    const videoRef = this.videoRef.current;
    const seekBarRef = this.seekBarRef.current;
    const time = videoRef.duration * (seekBarRef.value / 100);
    videoRef.currentTime = time - 10;
  };

  handleSeekbarChange = seekBar => {
    console.log(seekBar);
    const videoRef = this.videoRef.current;
    debugger;
    const time = videoRef.duration * (seekBar.target.value / 100);
    videoRef.currentTime = time;
  };

  handleVideoTimeUpdate = () => {
    const videoRef = this.videoRef.current;
    const seekBarRef = this.seekBarRef.current;
    const value = (100 / videoRef.duration) * videoRef.currentTime;
    seekBarRef.value = value;
  };

  handleMouseDown = () => {
    const videoRef = this.videoRef.current;
    videoRef.pause();
  };

  handleMouseUp = () => {
    const videoRef = this.videoRef.current;
    videoRef.play();
  };

  render() {
    console.log(this.props);
    if (this.seekBarRef.current) {
      console.log(this.seekBarRef.current.value);
    }
    return (
      <>
        {!this.props.live ? (
          <video
            onKeyUp={e => {
              console.log(e);
            }}
            onTimeUpdate={this.handleVideoTimeUpdate}
            id="custom_video"
            //   controls="controls"
            style={{
              position: "absolute",
              top: "0",
              left: "0",
              width: "100%",
              zIndex: 9
            }}
            ref={this.videoRef}
            // src="https://d3v28xe0wivrcx.cloudfront.net/out/v1/dd25dfe136e141fb8c576c9f17f133cb/index.m3u8"
            //   autoPlay
          >
            <source
              src="https://d3v28xe0wivrcx.cloudfront.net/out/v1/dd25dfe136e141fb8c576c9f17f133cb/index.m3u8"
              type="video/hls"
            />
          </video>
        ) : (
          <video
            onKeyUp={e => {
              console.log(e);
            }}
            onTimeUpdate={this.handleVideoTimeUpdate}
            style={{
              position: "absolute",
              top: "0",
              left: "0",
              width: "100%",
              zIndex: 9
            }}
            ref={this.videoRef}
            id="liveVideo"
          />
        )}
        <div id="vide_controls_bar" className="video_controls">
          <button
            style={{ width: "10vw", color: "white" }}
            id="playpausebtn"
            onClick={this.playPauseVideo}
          >
            Pause
          </button>
          <button style={{ width: "90vw", padding: "1rem" }}>
            <input
              style={{ width: "100%" }}
              ref={this.seekBarRef}
              onChange={this.handleSeekbarChange}
              onMouseDown={this.handleMouseDown}
              onMouseUp={this.handleMouseUp}
              type="range"
              id="seek-bar"
              value="0"
            />
          </button>
        </div>
      </>
    );
  }
}
export default PlayerView;
